.selected-row{

  .col-md-6{

    @media(max-width: $md-break){

      padding-bottom: 15px;

    }

    .selected-box{

      background: $grey;
      padding: 5px;
      height: 85px;
      border: thin solid $green;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: $curved-radius;
      position: relative;

      .overlay{

        background-color:rgba(193,205,35, 0.5);
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        border-radius: $curved-radius;

      }

      p{

        margin-top: 12px;

      }

      i{

        padding-left: 8px;

      }

    }

  }

}
